<template>
  <div id="login_div">
    <el-row :gutter="12">
      <el-col :span="8" :offset="8">
        <el-card shadow="always">
          <div id="login_card">
            <img v-if="!isLoading" src="@/assets/login_title.png">
            <img v-if="isLoading" src="@/assets/LoadingAnimation2.gif">
            <p><b>Username or email address</b></p>
            <el-input v-model="account"></el-input>
            <p><b>Password</b></p>
            <el-input v-model="password" show-password></el-input>
            <!-- <div class="error"><span>{{password_message}}</span></div> -->
            <button @click="check()" :disabled="isLoading" class="k-button-1 login-button">Sign in</button>
            <!-- <el-button class="k-buttton-1" round @click="check">Sign in</el-button> -->
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import axios from 'axios'
  import qs from 'qs'
  const api = axios.create({
    baseURL: process.env.VUE_APP_URL
  })
  export default {
    props:['id'],
    created(){

    },
    data() {
      return {
        account: '',
        password: '',
        password_message: '',
        isLoading: false
      }
    },
    methods: {
      check() {
        if (this.account == '' || this.password == '') {
          this.$message({
            message: 'Please enter account and password',
            type: 'error'
          })
        } else {
          const config = {
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded'
            }
          }
          var userInfo = {loginId: this.account}
          
          const url = '/auth/SignIn';
          this.isLoading = true;
          api.post(url, qs.stringify(userInfo),config)
          .then((response) => {
            if (response.status === 200) {
              if (this.password == response.data.Password) {
                this.$store.dispatch('setRoleData', response.data.Role)
                this.$store.dispatch('setUidData', response.data.Id)
                this.$store.dispatch('setNameData', response.data.Name)
                this.$store.dispatch('setAchivementsData', response.data.achivements)
                if(this.id){
                  this.$router.push({path: '/finding/'+this.id});
                } else {
                  this.$router.push({path: '/home/myinquiries'})
                }
              } else {
                this.isLoading = false;
                this.$message({
                  message: 'Wrong Password',
                  type: 'error'
                })
              }
            }
            console.log(response)
          })
          .catch((error) => {
            this.isLoading = false;
            if (error.message == 'Request failed with status code 404') {
              const e = 'Please check your username'
              this.$message({
                message: e,
                type: 'error'
              })
            }
            console.log(error.message)
          })
        }
      }
    },
  }
</script>

